<template>
  <div class="works max-container">
    <v-row>
      <v-col md="4" cols=12>
        <div class="works__tabs">
          <LeftTitle title="workLeftTitle"/>
          <div>
            <div class="works__tabs__title ">
              <span class="toggle-text-color"
                >{{$t('workTitle1')}}<br />
                {{$t('workTitle2')}}</span
              >
              <p class="toggle-text-color">
                {{$t('workDesc')}}
              </p>
            </div>
            <div class="m-inline-5">
              <div class="works__tabs__tab " v-for="(tab, i) in tabs" :key="i">
                <span
                  :class="tab.active ? 'tab-active' : 'toggle-text-color'"
                  @click="tabClickHandler(tab)"
                  >{{ tab.title }} </span
                ><span>/</span>
              </div>
            </div>
          </div>
        </div>
      </v-col>

      <v-col md="4" cols=6 v-for="(project, i) in filteredProjects" :key="i">
        <transition name="list">
          <div class="works__cover cursor-pointer" :key="project">
            <Tilt class="h-100" :options="{ glare: true, max: 2}">
              <div class="works__cover__layout"></div>
              <img :src="project.img" alt="" />
              <div class="works__cover__contain">
                <div class="works__cover__contain__top">
                  <span>{{ project.title }}</span>
                </div>
                <div class="works__cover__contain__bot">
                  <span>{{ project.desc }}</span>
                </div>
              </div>
            </Tilt>
          </div>
        </transition>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import LeftTitle from './LeftTitle'
export default {
  components: {
    LeftTitle
  },
  data: () => ({
    clientX: null,
    clientY: null,
    tabs: [
      { title: "VUE", active: true },
      { title: "HTML5", active: false },
      { title: "REACT", active: false },
    ],
    projects: [
      {
        title: "Project 1",
        img:
          "https://nafie.webrouk.com/html/assets/images/portfolio/portfolio-16.png",
        date: "Date 1",
        desc: "Desc 1",
        lang: "HTML5",
      },
      {
        title: "Project 2",
        img:
          "https://nafie.webrouk.com/html/assets/images/portfolio/portfolio-13.png",
        date: "Date 2",
        desc: "Desc 2",
        lang: "HTML5",
      },
      {
        title: "Project 3",
        img:
          "https://nafie.webrouk.com/html/assets/images/portfolio/portfolio-12.png",
        date: "Date 3",
        desc: "Desc 3",
        lang: "VUE",
      },
      {
        title: "Project 4",
        img:
          "https://nafie.webrouk.com/html/assets/images/portfolio/portfolio-10.png",
        date: "Date 4",
        desc: "Desc 4",
        lang: "REACT",
      },
      {
        title: "Project 5",
        img:
          "https://nafie.webrouk.com/html/assets/images/portfolio/portfolio-16.png",
        date: "Date 5",
        desc: "Desc 5",
        lang: "VUE",
      },
    ],
    filter: "VUE",
  }),
  computed: {
    filteredProjects() {
      return this.projects.filter((proj) => proj.lang === this.filter);
    },
  },
  methods: {
    tabClickHandler(tb) {
      this.tabs = this.tabs.map((tab) => {
        if (tb.title === tab.title) {
          tab.active = true;
          this.filter = tab.title;
        } else {
          tab.active = false;
        }
        return tab;
      });
    },
  },
};
</script>

<style lang="scss">
.works {
  padding: 100px 20px;

  &__tabs {
    display: flex;

    &__left {
      width: min-content;
      span {
        writing-mode: vertical-rl;
        color: #767676;
      }
      &__line {
        margin: auto;
        margin-top: 10px;
        position: relative;
        width: 1px;
        height: 50px;
        background: #767676;
      }
    }

    &__title {
      margin-inline-start: 20px;
      span {
        font-weight: 800;
        font-size: 34px;
        line-height: 44px;
      }
      p {
        font-weight: 300;
        opacity: 0.8;
        margin-top: 20px;
      }
    }

    &__tab {
      color: white;
      opacity: 0.8;
      transition: 0.3s;
      display: inline-block;
      span {
        &:nth-child(1) {
          cursor: pointer;
        }
        &:nth-child(2) {
          margin: 0px 10px;
          cursor: default;
        }
      }

      &:last-child {
        span {
          &:nth-child(2) {
            display: none;
          }
        }
      }
    }
    .tab-active {
      color: $secondary !important;
    }
  }

  &__cover {
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 300px;

    &__layout {
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100%;
      height: 100%;
      background: linear-gradient(
        0deg,
        rgba(255, 255, 255, 0) 0%,
        rgb(255, 255, 255) 100%
      );
      opacity: 0;
      transition: 0.3s;
    }
    &:hover & {
      &__layout {
        opacity: 0.2;
      }
    }

    img {
      width: 100%;
      height: 100%;
    }

    &__contain {
      div {
        position: absolute;
        padding: 7px;
        background: rgb(49, 49, 49);
        border-radius: 5px;
        left: 20px;
        transition: 0.4s;
        opacity: 0;

        span {
          color: white;
        }
      }
      &__top {
        bottom: -50px;
      }
      &__bot {
        bottom: -80px;
      }
    }
    &:hover & {
      &__contain {
        &__top {
          bottom: 70px;
          transition-delay: 0.1s;
          opacity: 1;
        }
        &__bot {
          bottom: 20px;
          transition-delay: 0.2s;
          opacity: 1;
        }
      }
    }
  }
  @media (max-width: 960px) {
    &__tabs{
      display: block;

      &__title{
        margin: 0px;
        margin-top: 10px;
      }
      &__tab{
        margin: 0px;
      }
    }
  }  
}
.list-enter-active,
.list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
</style>
